import "./App.css";
import { Link } from "react-router-dom";
import Login from "./pages/index";
import News from "./pages/News";

const Nav=()  =>{
    return (
        <header class="nav">
        <h1 class="largeT">News</h1> 
        
        <ul class="ul-nav">
{/*           
          <li class="li-nav"><Link to="/addNews">Add News</Link></li>
          <li class="li-nav"><Link to="/">Show News</Link></li>
          <li class="li-nav"><Link to="#">Extra</Link></li>
          */}
          <li class="li-nav"><a href={Login} >Add News</a></li>
          <li class="li-nav"><a href={News} >Show News</a></li>
          <li class="li-nav"><a href="#">Extra</a></li>
        </ul>
      </header>
    );

};

export default Nav;